<template>
  <div class="showContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <div class="box_request">
      <!-- <div class="box_white"> -->
        <h3>Upload photos</h3>
        <uploadImage ref="uploadImage" :showNum="1" class="marginleft"></uploadImage>
        <div class="box_line"></div>
        <div
          class="image_info"
        >Upload photos or just drag and drop face comparison picture here add 2 photos, JPG/JPEG/PNG ; 256*256 ~ 4096*4096, <2MB</div>
        <div class="image_submit">
          <a-button @click="gotoClear">Reset</a-button>
          <a-button type="primary" @click="gotoResult" htmlType="submit">Check</a-button>
        </div>
      </div>
      <section class="result" id="result">
        <p>Result</p>
        <div class="box_result">
          <div class="extraction">
            <circleImage ref="circleImage"></circleImage>
          </div>
        </div>
      </section>
    <!-- </div> -->
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import progressBar from "@/components/progressBar";
import uploadImage from "@/components/uploadImage/uploadImage";
import circleImage from "@/components/imageCanvas/circleImage";
export default {
  data() {
    return {
      routekey: ["findFaceRequest", "findFaceHistory;findFaceHistoryDetail"],
      formLayout: "inline",
      percentNum: 0
    };
  },
  components: {
    requestAndHisSubMenu,
    uploadImage,
    progressBar,
    circleImage
  },
  methods: {
    gotoClear() {
      this.$refs.uploadImage.clearAllImage();
      this.$util.showOrHideDiv("result", false);
    },
    gotoResult() {
      //  this.$refs.circleImage.showCircleImage('aaa',[[10, 10, 100, 100]])
      this.fileObj = this.$refs.uploadImage.getImageFile();
      console.log("gotoResult", this.fileObj);
      if (this.fileObj.fileImage) {
        if (this.fileObj.errImg == true) {
          this.$message.error(this.$t("verification.errorImg"));
        } else {
          console.log("gotoResult", this.fileObj.fileImage);
          let params = {
            image_file_1: this.fileObj.fileImage
          };
          this.$store.dispatch("getFindFace", params).then(
            res => {
              console.log("getFindFace face_list", res.data.face_list);
              this.$util.showOrHideDiv("result", true);
              this.getPosition(res.data.face_list);
            },
            error => {
              console.log("getFindFace error", error);
            }
          );
        }
      } else {
        this.$message.error(this.$t("main.pleaseUploadPicture"));
      }
    },
    //定位解析
    getPosition(backData) {
      let position = [];
      if (backData.length > 0) {
        for (let i = 0; i < backData.length; i++) {
          let itemData = backData[i] ? backData[i] : [];
          console.log("itemData", itemData);
          position[i] = [
            parseInt(itemData[0]),
            parseInt(itemData[1]),
            parseInt(itemData[2]) - parseInt(itemData[0]),
            parseInt(itemData[7]) - parseInt(itemData[1])
          ];
          console.log("position[i]:" + i + ",", position[i]);
        }
      } else {
        position = [[0, 0, 0, 0]];
      }
      console.log("position", position);
      this.$refs.circleImage.showCircleImage(this.fileObj.fileImage, position);
    }
  },
  mounted() {
    this.$util.showOrHideDiv("result", false);
  }
};
</script>
<style lang="less">
@import "../../../css/page.less";
</style>



